<template>
    <div>
        <div>
            <h2></h2><br>
            <el-row :gutter="20">
                <el-col :span="6" :offset="9">
                    <div>
                        <el-input v-model="username" placeholder=""></el-input><br><br>
                        <el-input placeholder="" v-model="password" show-password></el-input><br><br>
                        <el-button v-on:click="login" type="primary" round></el-button>
                        <br><span>:admin:123</span>
                    </div>
                </el-col>
            </el-row>
        </div>
    </div>
</template>

<script>
export default {
  data(){
      return {
          username: '',
          password:'',
      }
  },

   methods:{
      // 
      login(){
          var that = this;
          var url="/login/"+this.username+"/"+this.password;
          that.$axios
            .get(url)
            .then(function(resp) {
            if(resp.data.code==200){
                //
                var d=JSON.stringify(resp.data.data);//jsonstring
                var storage = localStorage;
                storage.setItem('user',d);
                that.$router.push("/home");
            }else{
                alert("/!!!");
            }
          })
            .catch(function (error) { // 
                console.log(error);
            });
      },
     
    }
}
</script>

