<template>
  <div>
    <div>
      <div align="left"><h2 style="color:	Gray"></h2></div>
      <el-row :gutter="20">
        <el-col :span="4" v-if="btcheck.searchButton">
          <el-input
            size="medium"
            placeholder=""
            suffix-icon="el-icon-edit el-input__icon"
            v-model="user.username">
          </el-input>
        </el-col>
        <el-col :span="4" v-if="btcheck.searchButton">
          <el-input
            size="medium"
            placeholder=""
            suffix-icon="el-icon-edit el-input__icon"
            v-model="user.name">
          </el-input>
        </el-col>
        <el-col :span="4" v-if="btcheck.searchButton">
          <el-select size="medium" v-model="user.userStatus" placeholder="">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-col>
        <el-col :span="4" v-if="btcheck.searchButton">
          <el-date-picker
            size="medium"
            value-format="yyyyMMdd"
            v-model="datavalue"
            type="daterange"
            start-placeholder="()"
            end-placeholder=""
            :default-time="['00:00:00', '23:59:59']">
          </el-date-picker>
        </el-col>
        <el-col :span="8">
          <el-button v-if="btcheck.searchButton"
          type="primary"
          @click="search()"></el-button>
          <el-button v-if="btcheck.addButton"
          @click="addUser()"></el-button>
          <el-button
          type="danger" v-if="btcheck.delButton"
          @click="deleteMore()"></el-button>
        </el-col> 
      </el-row>
    </div><br>
  <el-table
    ref="multipleTable"
    :data="tableData"
    stripe
    tooltip-effect="dark"
    style="width: 100%"
    @selection-change="handleSelectionChange">
    <el-table-column
      type="selection"
      width="55">
    </el-table-column>
    
    <el-table-column
      prop="id"
      label="">
    </el-table-column>
    <el-table-column
      prop="username"
      label=""
      show-overflow-tooltip>
    </el-table-column>
    <el-table-column
      prop="name"
      label=""
      show-overflow-tooltip>
    </el-table-column>
    <el-table-column
      prop="sex"
      label=""
      show-overflow-tooltip>
    </el-table-column>
    <el-table-column
      prop="userPhone"
      label=""
      show-overflow-tooltip>
    </el-table-column>
    <el-table-column
      prop="email"
      label=""
      show-overflow-tooltip>
    </el-table-column>
    <el-table-column
      label=""
      width="180">
      <template slot-scope="scope">
        <span style="margin-left: 10px">{{ timeFormat(scope.row.createTime) }}</span>
      </template>
    </el-table-column>
    <el-table-column
      label=""
      width="180">
      <template slot-scope="scope">
        <span style="margin-left: 10px">{{ timeFormat(scope.row.updateTime) }}</span>
      </template>
    </el-table-column>
    <el-table-column
      label=""
      width="180">
      <template slot-scope="scope">
        <span style="margin-left: 10px">{{ scope.row.userStatus===1?'':'' }}</span>
      </template>
    </el-table-column>
    <el-table-column label="" width="300" fixed="right">
      <template slot-scope="scope">
        <el-button
          size="mini"
          v-if="btcheck.updateButton"
          @click="handleEdit(scope.$index, scope.row)"></el-button>
        <el-button
          size="mini"
          type="danger"
          v-if="btcheck.delButton"
          @click="handleDelete(scope.$index, scope.row)"></el-button>
         <el-button
          size="mini"
          type="warning"
          v-if="btcheck.updateStatus"
          @click="updateStatusDialog(scope.$index, scope.row)">{{{1: '', 2:''}[scope.row.userStatus]}}</el-button>
        <el-button
          size="mini"
          v-if="btcheck.setRoleButton"
          @click="setRole(scope.$index, scope.row)"></el-button>
      </template>
    </el-table-column>
  </el-table>
  <div class="block">
    <br>
    <el-pagination
      @current-change="handleCurrentChange"
      :page-size="pageSize"
      background
      layout="total, prev, pager, next"
      :total="total">
    </el-pagination>
  </div>
  <!-- -->
    <el-dialog
      title=""
      :visible.sync="delDialog"
      width="30%"
      center>
      <span>{{delData}}</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="delDialog = false"> </el-button>
        <el-button type="primary" @click="deleteUser()"> </el-button>
      </span>
    </el-dialog>
   <!-- -->
    <el-dialog
      title=""
      :visible.sync="statusDialog"
      width="30%"
      center>
      <span></span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="statusDialog = false"> </el-button>
        <el-button type="primary" @click="updateStatusTo()"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data(){
    return{
      tableData:[],
      multipleSelection: [],
      total:0,
      user:{
        username:'',
        name:'',
        userStatus:'',
      },
      startTime:'',
      overTime:'',
      pageSize:10,//
      options: [
        {
          value: '',
          label: ''
        },
        {
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        }],
        datavalue:'',//-
        btcheck:{//
           searchButton:false,//
           addButton:false,//
           delButton:false,//
           updateButton:false,//
           updateStatus:false,//
           setRoleButton:false,//
        },
        delDialog:false,//
        delData:[],//
        statusDialog:false,//
        updateStatus:{id:'',userStatus:''},//
    }
  },
  mounted(){
         this.searchdata(1);
         this.updatePowers();
  },
  methods: {
    //
    setRole(index, row){
      var that = this;
      var storage = localStorage;
      storage.setItem('setRoleUser',JSON.stringify(row));
      that.$router.push('/home/setRole');
    },
    //
    updateStatusDialog(index, row){
      var that = this;
      that.statusDialog = true;  
      that.updateStatus.id = row.id;
      if(row.userStatus == 1){
        that.updateStatus.userStatus = 2;
      };
      if(row.userStatus == 2){
        that.updateStatus.userStatus = 1;
      }
    },
    //
    updateStatusTo(){
      var that = this;
      var url="/updateUser";
      let param = new URLSearchParams();
      param.set("id",that.updateStatus.id);
      param.set("userStatus",that.updateStatus.userStatus);
       that.$axios
              .post(url,param)
              .then(function(resp) {
               if(resp.data.success){
                that.statusDialog = false; 
                that.searchdata(1);
               }
            })
              .catch(function (error) { // 
                  console.log(error);
            });
    },
    //
    addUser(){
      var that = this;
      that.$router.push('/home/addUser');
    },
    //
    updatePowers(){
      var that = this;
      var storage = localStorage;
      var powers = JSON.parse(storage.getItem('powers'));
      console.log(powers);
      for(var j=0,len=powers.length;j<len;j++){
        if(powers[j].id == 5){
          that.btcheck.searchButton = true;
        }
        if(powers[j].id == 6){
          that.btcheck.addButton = true;
        }
        if(powers[j].id == 7){
          that.btcheck.delButton = true;
        }
        if(powers[j].id == 8){
          that.btcheck.updateButton = true;
        }
        if(powers[j].id == 17){
          that.btcheck.updateStatus = true;
        }
        if(powers[j].id == 18){
          that.btcheck.setRoleButton = true;
        }
      }
      console.log(powers);
    },
    //
    search(){
      var that = this;
      if(that.datavalue[0]!=undefined){
        that.startTime=that.datavalue[0]+"000000";
        that.overTime=that.datavalue[1]+"235959";
      }
      console.log(that.startTime,that.overTime);
      this.searchdata(1);
    },
    //
    searchdata(pageNumber){
         var that = this;
          var url="/queryUserPage";
          let param = new URLSearchParams();
          param.append("pageSize",that.pageSize);
          param.append("pageNumber",pageNumber);
          param.append("username",that.user.username);
          param.append("name",that.user.name);
          param.append("userStatus",that.user.userStatus);
          param.append("startTime",that.startTime);
          param.append("overTime",that.overTime);
          that.$axios
            .post(url,param)
            .then(function(resp) {
              that.tableData = resp.data.data;
              that.total = resp.data.total;
          })
            .catch(function (error) { // 
                console.log(error);
          });
    },
      //
      handleSelectionChange(val) {
        this.multipleSelection = val;
        
      },
      //
      handleEdit(index, row) {
        console.log(index, row);
        var that = this;
        var storage = localStorage;
        storage.setItem('updateUser',JSON.stringify(row));
        that.$router.push('/home/updateUser');
      },
      //
      handleDelete(index, row) {
        this.delData=[];
        this.delDialog = true;
        this.delData.push(row.id);
      },
      //
      deleteMore(){
        this.delDialog = true;
        this.delData=[];
        console.log(this.multipleSelection);
        for(var j=0 ,len=this.multipleSelection.length;j<len;j++){
           this.delData.push(this.multipleSelection[j].id);
        }
         console.log(":"+this.delData);
      },
      //
      deleteUser(){
        var that = this;
        var data = that.delData;
          var url="/deleteUser";
          let param = new URLSearchParams();
          param.append("uid",that.delData);
          that.$axios
            .post(url,param)
            .then(function(resp) {
             if(resp.data.success){
               that.delDialog = false;
               that.searchdata(1);
             }
          })
            .catch(function (error) { // 
                console.log(error);
          });
      },
     //
      handleCurrentChange(val) {
        console.log(`: ${val}`);
        this.searchdata(val);
      },
    //
      timeFormat (time){
           if (!time) {
              return '';
          }
          return time.replace(/(\d{4})(\d{2})(\d{2})(\d{2})(\d{2})(\d{2})/g, "$1-$2-$3 $4:$5:$6");
      },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>
