import Vue from 'vue'
import App from './App.vue'
//element ui
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
//axios
import axios from 'axios'
//
import Login from './components/login.vue'
import HelloWorld from './components/HelloWorld.vue'
import Home from './components/home.vue'
import User from './components/user.vue'
import Role from './components/role.vue'
import List from './components/list.vue'
import RolePermissions from './components/rolePermissions.vue'
import AddUser from './components/addUser.vue'
import AddMenu from './components/addMenu.vue'
import AddRole from './components/addRole.vue'
import UpdateUser from './components/updateUser.vue'
import UpdateRole from './components/updateRole.vue'
import UpdateMenu from './components/updateMenu.vue'
import SetRole from './components/setRole.vue'

//
import VueRouter from 'vue-router'
//
Vue.use(VueRouter);
//axios
Vue.prototype.$axios = axios
//element ui
Vue.use(ElementUI)
Vue.config.productionTip = false
//axios.defaults.baseURL="/apis"// 
//
const router = new VueRouter({
  mode: 'hash',
  routes: [
   {path:'/',name:'login',component:Login},//
  //  {
  //    path:'/',
  //    name:'home',
  //    component:Home,
  //   },
    {
      path:'/home',
      name:'home',
      component:Home,
      children:[
        {path:'/home/hello',component:HelloWorld}, //
        {path:'/home/user',component:User},//
        {path:'/home/rolePermissions',name:'rolePermissions',component:RolePermissions},//
        {path:'/home/role',component:Role},//
        {path:'/home/list',component:List},//
        {path:'/home/addUser',component:AddUser},//
        {path:'/home/updateUser',component:UpdateUser},//
        {path:'/home/addMenu',component:AddMenu},//
        {path:'/home/updateMenu',component:UpdateMenu},//
        {path:'/home/addRole',component:AddRole},//
        {path:'/home/updateRole',component:UpdateRole},//
        {path:'/home/setRole',component:SetRole},//
       ]
     },
     //
     {path:'/login',name:'login',component:Login},
     //
   {path:'*',redirect:'/'} //404
   //
  ]
 });

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
